import emailjs from 'emailjs-com';
const Mailer = () => {
//  make function
     function sendEmail(e){
         e.preventDefault();


         emailjs.sendForm('service_db1q2v7','template_m71gthy',e.target,
         'user_fLJK5K4epIdhxVr5UGBeR'
         ).then(res => {
             console.log(res);
         }).catch(err => console.log(err));

     }

    return(
        <div className='container border'
        style={{marginTop:'50px',
        width:'50%',
        // backgroundImage= {logo},
        // backgroundImage=url('https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/rm21-background-tong-058.jpg?w=800&dpr=1&fit=default&crop=default&q=65&vib=3&con=3&usm=15&bg=F4F4F3&ixlib=js-2.2.1&s=710a6fed5b1923da8d5f95191839ef8a'), 
        backgroundPosition:'center',
        backgroundSize:'cover',
        }}>
            {/* <h1 style ={{marginTop:'25px'}}>Contact Form</h1> */}
            <form className='row' style={{margin:'25px 85px 75px 100px'}}
            onSubmit={sendEmail}>
                
                <input type='text' name='name' className='form-control' placeholder='your name' required/>
               
                <input type='email' name='user_email' className='form-control' placeholder='your email' required/>
               
                <textarea name='message' rows='4' className='form-control' placeholder='message' required/>
                <input type='submit' value='send' className='form-control btn btn-primary ' placeholder='message'/>
                <h4 class = 'text-primary text-center'>click send just once</h4>

            </form>
        </div>
    )

}
export default Mailer;