import React from 'react'

const Masthead = () => {
    return (
        <header class="masthead">
            <div class="container">
                <div class="masthead-subheading">Welcome To My Studio!!!</div>
                <div class="masthead-heading text-uppercase"> Nice To Meet You !!!</div>
                <a class="btn btn-primary btn-xl text-uppercase" href="#services">Let's Begin</a>
            </div>
        </header>
    )
}

export default Masthead
